import React, {useState} from "react";
import axios from "axios";

function Form()
{
    let [classNamesEmail, setClassesEmail] = useState("emailAnimOff");
    let [classNamesPhone, setClassesPhone] = useState("emailAnimOff");
    let [hasDataEmail, setHasDataEmail] = useState(false);
    let [hasDataPhone, setHasDataPhone] = useState(false);
    let [typeOfService, setTOS]= useState(0);

    let [renderLoading, setRenderLoading] = useState(false);
    let [renderMessage, setRenderMessage] = useState(false);
    let [alertMessage, setAlertMessage]=useState("");


    async function sendData(event)
    {
        let eventRef = event.target;
        setRenderLoading(true);
        setRenderMessage(false);
        event.preventDefault();
        let data = new FormData(event.target).entries();
        let jsonData= {}
    
        //data is an iterator object and hence im pulling values from it in this for loop
        //and a json object is created to be posted to the request route
        for(var [key,value] of data) {
            jsonData[key] = value; 
         }
    
         let response;
         

         try
         {
            
            response = await axios.post("https://warm-citadel-88564.herokuapp.com/submit",jsonData);
            if(response)
            {
                eventRef.reset();
                setRenderLoading(false);
                setRenderMessage(true);
                setAlertMessage(response.data);
                setClassesEmail("emailAnimOff");
                setClassesPhone("emailAnimOff");
            }
             
         } catch(err)
         {
             setRenderLoading(false);
             setRenderMessage(true);
             setAlertMessage("There was an error with our server. Please try again later");
         }
       
    }


    return(
        <div className="pageContainer">

{renderMessage && <div className="messageContainer">
    <h5>{alertMessage}</h5>
</div>
}



<div className="formContainer">

    <img className="formImg" src="images/formImgs/keyboard.jpg"></img>

<div className="form">
    <h3>MagnaTech Xystems</h3>
    <h5>Request a Custom Build/Service</h5>
        <form onSubmit={sendData} name="formData">

        <div className="inputSection">
            <input type="text" name="name" autoComplete="off" required/>
            <label htmlFor="name" className="label-name">
            <span className="content-name">Name</span>
            </label>
        </div>

        <div className="inputSection">
            <input type="text" name="phone" autoComplete="off" pattern="[0-9]+" title="Please enter number as digits" required
                onFocus={()=> setClassesPhone("emailAnimOn")} 
        onBlur={()=> {
           if(!hasDataPhone) setClassesPhone("emailAnimOff")
            }}
        onChange={
                    (event)=> {
                        if(event.target.value==="") setHasDataPhone(false); else setHasDataPhone(true);
                    }
                } 
            />
            <label htmlFor="phone" className={classNamesPhone}>
            <span className="content-name">Phone No.</span>
            </label>
        </div>

        <div className="inputSection">
        <input type="email" name="email" autoComplete="off" required
        onFocus={()=> setClassesEmail("emailAnimOn")} 
        onBlur={()=> {
           if(!hasDataEmail) setClassesEmail("emailAnimOff")
            }}
        onChange={
                    (event)=> {
                        if(event.target.value==="") setHasDataEmail(false); else setHasDataEmail(true);
                    }
                }    
        />
            
            <label className={classNamesEmail} htmlFor="email">
            <span className="content-name">Email</span>
            </label>
        </div>

        <div className="typeOfServiceSection">
            <label htmlFor="options">
            <span className="content-options">Type of Service:</span>
            </label><br></br>
        <select name="options" onChange={(event) => {setTOS(parseInt(event.target.value));}}>
            <option value="0">Servicing/Repair</option>
            <option value="1">Custom Build</option>
        </select>
        </div>

        <div className="textBoxInputSection">
        <label htmlFor="textBoxInputSection" className="label-name">
            <span className="content-textbox" >{!typeOfService ? "How may we help you?" : "Software/Games you want to run"}</span>
            </label><br></br>
        <textarea name={!typeOfService ? "description" : "softwares"} placeholder={!typeOfService ? "Describe the issue" : "Seperate each software/game with a comma or a by a new line" } autoComplete="off" required/>
            
        </div>

        {typeOfService ?  <div className="textBoxInputSection">
        <label htmlFor="comments" className="label-name" >
            <span className="content-textbox">Additional Comments</span>
            </label>
        <textarea name="comments" autoComplete="off" placeholder="Particular case colour, RGB strips or any specific requirements you'd like us to implement. Describe it here"/>
            
        </div> : null}

        
            <button type="submit" className="formButton" disabled={renderLoading}>Submit</button>
            {renderLoading &&<img src="icons/loading.svg" className="formLoading"></img>}
        

        

        </form>
    </div>
    </div>
        </div>
    
    
    )
}


export default Form;